*{
    box-sizing: border-box;
}

body{
    background-color: white;
}

h1{
    text-align: center;
}

.form{
    background-color: white;
    padding: 15px;
    text-align: center;
}

.form input{
    padding: 10px;
    margin: 5px;
}
.form button{
    padding: 10px;
    cursor: pointer;
}

.container {
    position: relative;
    left: 50%;
    transform: translate(-50%);
    background-color: cyan;
    color: white;
    box-shadow: 0 0 2px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 90%;
    margin-top: 20px;
    text-indent: 10px;
}

.container input {
    width: 10%;
}

.delete{
    margin-top: 5%;
    position: relative;
    left: 50%;
    transform: translate(-50%);
    padding: 10px;
    cursor: pointer;
    background-color: white;
    color: blue;
    border: 1px solid white;
    box-shadow: 0 0 2px blue;
}